import {
    Card,
    Form,
    Input,
    Button,
} from 'antd-mobile'

import { useNavigate } from 'react-router-dom';

import './index.scss'

export default function Login() {
    const navigate = useNavigate(),
        onFinish = (values) => {
            navigate(`/home?value=${JSON.stringify(values)}`)
        };

    const strDate = (date) => {
        const month = date.getMonth() + 1,
            day = date.getDate()
        return `${month < 10 ? '0' + month : month
            }-${day < 10 ? '0' + day : day}`
    }

    const date = new Date(),
        leaveDate = strDate(date)
    date.setDate(date.getDate() + 1)
    const backDate = strDate(date)

    /*  const onFinishFailed = (errorInfo) => {
         console.log('Failed:', errorInfo);
     }; */


    return (
        <div className='Login'>
            <Card
                title=""
                bordered={false}
                className='loginCard'
            >

                <Form
                    onFinish={onFinish}
                    layout='horizontal'
                    footer={
                        <Button block type='submit' color='primary' size='large'>
                            
                        </Button>
                    }
                >
                    <Form.Header></Form.Header>
                    <Form.Item
                        label=""
                        name="stuName"
                        initialValue=""
                        rules={[
                            { required: true, message: '' },
                            {
                                pattern: /^[\u4e00-\u9fa5]{0,}$/,
                                message: ',',
                                validateTrigger: 'onBlur' // 
                            }
                        ]}
                    >
                        <Input clearable />
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="stuNum"
                        initialValue="1815041038"
                        rules={[
                            { required: true, message: '' },
                            {
                                pattern: /^[0-9]+$/,
                                message: ',',
                                validateTrigger: 'onBlur' // 
                            }
                        ]}
                    >
                        <Input clearable />
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="outDay"
                        initialValue={leaveDate}
                        rules={[
                            { required: true, message: '' },
                            {
                                pattern: /^([0-9]{1,2})+\-[0-9]{1,2}$/,
                                message: 'XX-XX,',
                                validateTrigger: 'onBlur' // 
                            }
                        ]}
                    >
                        <Input clearable />

                    </Form.Item>
                    <Form.Item
                        label=""
                        name="outDate"
                        initialValue="12:00"
                        rules={[
                            { required: true, message: '' },
                            {
                                pattern: /^([0-9]{2})+\:[0-9]{2}$/,
                                message: 'XX:XX,',
                                validateTrigger: 'onBlur' // 
                            }
                        ]}
                    >
                        <Input clearable />
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="inDay"
                        initialValue={backDate}
                        rules={[
                            { required: true, message: '' },
                            {
                                pattern: /^([0-9]{2})+\-[0-9]{2}$/,
                                message: 'XX-XX,',
                                validateTrigger: 'onBlur' // 
                            }
                        ]}
                    >
                        <Input clearable />
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="inDate"
                        initialValue="12:00"
                        rules={[
                            { required: true, message: '' },
                            {
                                pattern: /^([0-9]{2})+\:[0-9]{2}$/,
                                message: 'XX:XX,',
                                validateTrigger: 'onBlur' // 
                            }
                        ]}
                    >
                        <Input clearable />
                    </Form.Item>
                </Form>
            </Card >
        </div >
    )
}
